	module.exports = {"data":{"markdownRemark":{"html":"<h2>C# </h2>\n<p>C Sharp, more commonly referred to as “C#”, is a general-purpose, object-oriented programming language. C# was developed by Anders Hejlsberg and his development team at Microsoft and is currently on version 7.0.</p>\n<p>C# has its roots in the family of C languages. It inherits most of its feature from C, C++, and Java. For this reason, programmers familiar with C/C++/Java may be able to get up to speed with C# in a shorter timespan.</p>\n<p>C# is an object-oriented language while also providing support for component-oriented programming.</p>\n<h4>Features of C# include:</h4>\n<ol>\n<li>Automatic Garbage Collection</li>\n<li>Exceptional Handling</li>\n<li>Type-safety</li>\n<li>Versioning</li>\n<li>Delegates</li>\n<li>Properties</li>\n<li>LINQ (Language-Integrated Query) and Lambda Expressions</li>\n<li>Generics</li>\n<li>Indexers</li>\n<li>Multithreading</li>\n</ol>\n<h4>New Features Added on C# 7.0:</h4>\n<ol>\n<li>Deconstructors</li>\n<li>New syntax to work with Tuples</li>\n<li>Pattern Matching with Is Expressions</li>\n<li>Local Functions</li>\n<li>Return by Reference</li>\n<li>Out Variables</li>\n<li>Literal improvements</li>\n<li>Generalized Async Return Types</li>\n<li>More Expresion-Bodied Members</li>\n<li>Throw Expressions</li>\n</ol>\n<p>You can use C# to create Windows client applications, XML Web services, distributed components, client-server applications, database applications, and much more.</p>\n<h4>More Information:</h4>\n<ul>\n<li><a href='https://docs.microsoft.com/en-us/dotnet/csharp/getting-started/introduction-to-the-csharp-language-and-the-net-framework' target='_blank' rel='nofollow'>Introduction to C#</a></li>\n<li><a href='https://www.microsoft.com/net/tutorials/csharp/getting-started' target='_blank' rel='nofollow'>C# Tutorials</a></li>\n<li><a href='https://docs.microsoft.com/en-us/dotnet/csharp/' target='_blank' rel='nofollow'>Official C# Documentation</a></li>\n<li><a href='https://msdn.microsoft.com/en-us/magazine/mt790184.aspx' target='_blank' rel='nofollow'>New Features in C# 7.0</a></li>\n</ul>","fields":{"slug":"/csharp/"},"frontmatter":{"title":"C#"}}},"pathContext":{"slug":"/csharp/"}}

